body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
}

.container {
    height: 100vh;
    width: 100vw;
    background-color: white;
}

h1 {
    margin: 0;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.hero {
    margin:auto;
    display: block;
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 50vw;
    height: 50vw;
    border-radius: 50%;
    box-shadow: 0 0 0 rgba(115,209,200, 0.4);
    -webkit-animation: pulse 5s infinite;
            animation: pulse 5s infinite;
}
.speechOut {
    font-family: Helvetica;
    font-weight: bold;
    font-size: 8vw;
    text-align: center;
    position: absolute;
}
@-webkit-keyframes pulse {
    0% { -webkit-box-shadow: 0 0 0 0 rgba(115,209,200, 0.4); }
    70% { -webkit-box-shadow: 0 0 0 30vh transparent }
    100% { -webkit-box-shadow: 0 0 0 0 transparent }
}
@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 rgba(115,209,200, 0.4);
    }
    70% {
        box-shadow: 0 0 0 30vh transparent;
    }
    100% {
        box-shadow: 0 0 0 0 transparent;
    }
}
.body {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 10px;
    padding-top: 10px;
}

.footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-top: 60px;
}

.footer-button {
    width: 140px
}

.voice-box {
    padding-left: 60px;
}

.input {
    display: inline-flex;
    padding-left: 10px;
    padding-right: 10px;
}

.iconButton {
    display: inline-flex;
}

.paper-container {
    padding: 30px;
}

.paper-modal {
    padding: 10px;
}

.modal-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.modal-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding: 15px;
}

.section {
    flex: 1 1;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
}

.buttons {
    padding-left: 20px;
}

h4 {
    display: inline-flex;
    margin: 0;
    padding-bottom: 7px;
    font-weight: normal;
}


.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.fab-container {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  justify-content: space-evenly;
}
